.nav {
  &__list {
    margin: 0;
    padding: 0;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__link {
    color: rgba(29, 29, 29, 1);
    text-decoration: none;
    font-size: 22px;
    line-height: 22px;
    letter-spacing: 2px;

    &:hover {
      border-bottom: 1px solid rgba(29, 29, 29, 1);
    }
  }
}
